ul {
    list-style-type: none;
    margin: 0;
    margin-left: 5%;
    margin-right: 5%;
    padding: 0;
    overflow: hidden;
    background-color: #fdda6f;
    color: #d73725;
}

li {
    float: left;
}

.nav>li>a {
    font-family: 'Kranky', cursive;
    font-size: 1.5em;
    -webkit-transform: rotate(-5deg);
        -ms-transform: rotate(-5deg);
            transform: rotate(-5deg);
    display: block;
    color: #d73725;
    text-align: center;
    padding: 35px 28px;
    text-decoration: none;
}

.nav>li>a:focus,
.nav>li>a:hover {
    color: #c9a7a0;
    background-color: #fdda6f;
    -webkit-transform: rotate(-17deg);
        -ms-transform: rotate(-17deg);
            transform: rotate(-17deg);
}

.nav>li>a:active {
    background-color: #fdda6f;
}

.brand {
    font-family: 'Kranky', cursive;
    font-size: 3em;
    -webkit-transform: rotate(-7deg);
        -ms-transform: rotate(-7deg);
            transform: rotate(-7deg);
    display: block;
    text-align: center;
    padding: 28px 28px;
}

.navbar {
    -webkit-box-shadow: 0px 5px 11px 0px #747171;
    box-shadow: 0px 5px 11px 0px #747171;
    min-height: 100px;
    background-color: #fdda6f;
    margin-bottom: 0px;
    border-style: solid;
    border-color: #d73725;
    position: absolute;
    top: 0px;
    width: 100%;
    z-index: 2;
}

.navbar-default{
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #fdda6f),to(#fdda6f));
    background-image: -webkit-linear-gradient(top,#fdda6f 0,#fdda6f 100%);
    background-image: -o-linear-gradient(top,#fdda6f 0,#fdda6f 100%);
    background-image: linear-gradient(to bottom,#fdda6f 0,#fdda6f 100%);
}



