.img-container {
  display: -ms-inline-flexbox;
  display: inline-flex;
  max-height: 200px;
  background-color: white;
  margin: 30px auto 20px;
  padding: 10px;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
}

.img-container > img {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: block;
  max-height: 150px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.05, 1.6, 0.85, 1.2);
       -o-transition-timing-function: cubic-bezier(0.05, 1.6, 0.85, 1.2);
          transition-timing-function: cubic-bezier(0.05, 1.6, 0.85, 1.2);
}

.img-container > img:hover {
 -webkit-transform: scale(1.7);
 -ms-transform: scale(1.7);
     transform: scale(1.7);
} 


