.profile-container {
  font-family: 'Gloria Hallelujah', cursive;
  font-size: 18px;
  width: 40%;
  background-color: #fdda6f;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px auto 30px auto;
  border-style: solid;
  border-color: #d73725;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-shadow: 12px 12px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  box-shadow: 12px 12px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
}

.user-info-container{
  border-color: #d73725;
  border-style: solid;
  background-color: white;
  width: 70%;
  margin: auto;
  margin-top: 40px;
}

.user-info {
  padding: 5px;
  margin: 10px;
  border: solid;
  border-width: 1px;
  border-color: #fdda6f;
}



